import React from 'react';
import InfoTest from './InfoTest';
import { useTranslation } from "react-i18next";
import Button from './UI/Button/Button';
import { StyledCallToAction, StyledH1, StyledP } from './styles/CallToAction.styled';


const CallToAction = () => {
    const { t } = useTranslation();
    return (
        <StyledCallToAction id="CallToAction">
            <StyledH1>{t("__Frontend_")}</StyledH1>
            <InfoTest/>
            <StyledP>{t("_")}</StyledP>
            <StyledP>{t("_")}</StyledP>
            <Button type="Link" goToPage='quiz' textBtn=" " pageName='homepage'/>             
        </StyledCallToAction>
    );
};

export default CallToAction;

